import meSideMenu from "@/components/meSideMenu";
import { TAG_LIST, TAG_IS_EXISTS, ADD_DEALS, GET_COUNTRY_LIST } from '@/api/api';
import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
export default {
  name: "jobs",
  components: {
    meSideMenu
  },

  data() {
    return {
      submitLoadingValue: false,
      mapLoading: false,
      map1Loading: false,
      accessToken: process.env.VUE_APP_MAP_BOX_ACCESS_TOKEN,
      mapStyle: process.env.VUE_APP_MAP_BOX_STYLE,
      dealLocationTypeValue: "1",
      userInfo: {},
      basicUserInfo: {},
      tagsData: [],
      addTagsStatus: false,
      ownTagsValue: '',
      ownTagsList: [],
      selectTagsList: [],
      selectTagsArr: [],
      tagsCnData: [],
      tagsEnData: [],
      countryObj: {},
      provinceObj: {},
      cityObj: {},
      countryName: '',
      countryNameCn: '',
      provinceName: '',
      provinceNameCn: '',
      cityName: '',
      cityNameCn: '',
      countryOptions: [],
      provinceOptions: [],
      cityOptions: [],
      basicForm: {
        token: localStorage.getItem('token'),
        user_id: localStorage.getItem('uid'),
        title: '',
        desc: '',
        tag: '',
        is_online: 0,
        location: '',
        country_id: '',
        state_id: '',
        town_id: '',
        lng: '',
        lat: '',
        tags_cn: '',
        tags_en: ''
      },
      basicRules: {
        title: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        desc: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }]
      },
      dealLocationType: 1
    };
  },

  mounted() {
    this.getTagsList();
    this.getAllCountry();
  },

  methods: {
    changeDealLocationType(value) {
      this.dealLocationType = value;
      let self = this;

      if (value == 2) {
        this.mapLoading = true;
        setTimeout(function () {
          self.initMap();
          self.mapLoading = false;
        }, 1000);
      }

      if (value == 3) {
        this.map1Loading = true;
        setTimeout(function () {
          self.initMap1();
          self.map1Loading = false;
        }, 1000);
      }
    },

    initMap() {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer",
        center: [121.472644, 31.231706],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker(); // {
      //   draggable:true
      // }
      // .setLngLat([121.47, 31.23])
      // .addTo(map);
      // marker.on('dragend',(e)=>{
      //   console.log(e)
      // })

      geocoder.on('result', e => {
        console.log(e);
        marker.setLngLat(e.result.center).addTo(map);
        this.basicForm.location = e.result.place_name;
        this.basicForm.lng = e.result.center[0];
        this.basicForm.lat = e.result.center[1];
      });
      geocoder.on('clear', e => {
        console.log(e);
        this.basicForm.location = '';
        this.basicForm.lng = '';
        this.basicForm.lat = '';
      });
    },

    initMap1() {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer1",
        center: [121.472644, 31.231706],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker(); // {
      //   draggable:true
      // }
      // marker.on('dragend',(e)=>{
      //   console.log(e)
      // })

      geocoder.on('result', e => {
        console.log(e);
        marker.setLngLat(e.result.center).addTo(map);
        this.basicForm.location = e.result.place_name;
        this.basicForm.lng = e.result.center[0];
        this.basicForm.lat = e.result.center[1];
      });
      geocoder.on('clear', e => {
        console.log(e);
        this.basicForm.location = '';
        this.basicForm.lng = '';
        this.basicForm.lat = '';
      });
    },

    getAllCountry() {
      let params = {};
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.countryOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllProvinces(countryId) {
      let params = {
        country_id: countryId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.provinceOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllCitys(countryId, stateId) {
      let params = {
        country_id: countryId,
        state_id: stateId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.cityOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    countryChange(e) {
      console.log(e);
      this.basicForm.state_id = undefined;
      this.basicForm.town_id = undefined;
      this.provinceOptions = [];
      this.cityOptions = [];
      this.basicForm.country_id = e.id;
      this.countryName = e.name;
      this.countryNameCn = e.name;
      this.getAllProvinces(e.id);
    },

    provinceChange(e) {
      console.log(e);
      this.basicForm.town_id = undefined;
      this.cityOptions = [];
      this.basicForm.state = e.id;
      this.provinceName = e.name;
      this.provinceNameCn = e.name;
      this.getAllCitys(this.basicForm.country_id, e.id);
    },

    cityChange(e) {
      console.log(e);
      this.basicForm.town_id = e.id;
      this.cityName = e.name;
      this.cityNameCn = e.name;
    },

    getTagsList() {
      let data = {
        page: 1,
        limit: 1000
      };
      TAG_LIST(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.tagsData = res.message.data;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    addOwnTag() {
      this.addTagsStatus = false;
      let token = localStorage.getItem('token');
      let existData = {
        token: token,
        tag_name: this.ownTagsValue
      };
      TAG_IS_EXISTS(existData).then(res => {
        console.log(res);

        if (res.code == 200) {
          if (res.message == 0) {
            let ownIndex = this.ownTagsList.indexOf(this.ownTagsValue);

            if (ownIndex == -1) {
              this.ownTagsList.push(this.ownTagsValue);
            }

            let index = this.selectTagsList.indexOf(this.ownTagsValue);

            if (index == -1) {
              // this.selectTagsList.splice(index, 1, obj);
              this.selectTagsList.push(this.ownTagsValue);
              this.tagsCnData.push(this.ownTagsValue);
              this.tagsEnData.push(this.ownTagsValue);
            } else {
              this.selectTagsList.splice(index, 1);
              this.tagsCnData.splice(index, 1);
              this.tagsEnData.splice(index, 1);
            }

            this.ownTagsValue = '';
          }
        }
      });
    },

    selectTagA(item) {
      let index = this.selectTagsList.indexOf(item.id);

      if (index == -1) {
        this.selectTagsList.push(item.id);
        this.tagsCnData.push(item.name_cn);
        this.tagsEnData.push(item.name_en);
      } else {
        this.selectTagsList.splice(index, 1);
        this.tagsCnData.splice(index, 1);
        this.tagsEnData.splice(index, 1);
      }
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.basicForm.tag = this.selectTagsList;
          this.basicForm.tags_cn = this.tagsCnData.join(',');
          this.basicForm.tags_en = this.tagsEnData.join(',');
          this.$loading({
            text: 'Loading...'
          });
          let params = Object.assign({}, this.basicForm);
          ADD_DEALS(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              this.$router.push('/deals/myDeals');
              this.$loading().close();
            }
          }).catch(err => {
            console.log(err);

            if (err.msg) {
              this.$message.error(err.msg);
            }

            if (err.message) {
              this.$message.error(err.message);
            }
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    discard() {
      this.$router.go(-1);
    }

  }
};